<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process231"></a>

<p><br></p>
<p><span class="rvts17">受苦者（</span><span class="rvts50">Bound By Pain</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:281,Y:398</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：被附身的矮人（</span><span class="rvts20">The Possessed Dwarf</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">消灭附身的恶魔，拯救矮人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1310.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">血月岛中部有</span><span class="rvts20">3</span><span class="rvts21">处雕像，破坏雕像的前提是先要找到一本叫做《华丽诗集》的书，这本书位于血月岛的很多地方。其中，小岛东北部有一处黑环势力占领的区域，有这本书。在血月岛大树的黑环团伙中，也有这本书。</span>
<br><span class="rvts21">另外，在接贾韩的支线</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人（</span><span class="rvts20">A Hunter of Wicked Thing</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的屋子内，也有这本书。这本书需要有</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签的角色读懂后，才可以用于破除雕像的诅咒。</span>
<br><span class="rvts21">接着使用必须是携带银质杠杆的角色与门阀互动，才可以成功合成杠杆门阀。使用杠杆前，需要使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">查看守卫洞穴的灵魂，说服他或杀掉他，才会使他失去对杠杆的源力控制，这样才能打开对应的石门。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1311.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">破除矮人身上的</span><span class="rvts20">4</span><span class="rvts21">根柱子，释放恶魔。尝试击败矮人，但不要杀掉它，把它打到血量在</span><span class="rvts20">15%</span><span class="rvts21">左右，恶魔会从附魔的矮人身体里出来，并钻进角色队伍中的任何角色身体里。</span>
<br><span class="rvts21">使用同样的方法，逼出恶魔后，这个恶魔需要</span><span class="rvts20">3</span><span class="rvts21">次左右的随机附身（附身到队伍同伴身上），注意随时保护好队友和矮人</span><span class="rvts20">NPC</span><span class="rvts21">的安全，完成任务。由于该任务比较艰难，可以尝试直接杀掉矮人。但这样将使任务关闭，并无法触发矮人的宝藏的额外经验。</span>
<br><span class="rvts21">完成任务后，矮人会告诉我们他埋藏的宝藏的位置。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1312.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">矮人在血月岛靠近死亡沙滩上掩埋了一个钱袋，里面只有</span><span class="rvts20">2</span><span class="rvts21">个金币。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



